@media screen and (max-width: 768px) {
  .flickity-prev-next-button {
    display: inline-block; } }

@media screen and (max-width: 768px) {
  .photo-of-me {
    margin-left: 0 inherit 0 inherit; }
  .my-work {
    height: auto; }
  .home-page-images-of-portfolios {
    width: 100%;
    padding: 20px;
    float: none; }
